<template>
  <div id="step-4" class="animate__animated animate__backInUp" v-loading="loading" element-loading-text="正在等待 NFC 校验..." element-loading-background="rgba(243, 241, 235, 0.5)" element-loading-spinner="el-icon-loading">
    <br />
    <div style="
        color: #822E84;
        font-size: 15px;
        border: 1px solid;
        border-radius: 15px;
        padding: 5px 10px;
        text-align: center;
      ">
      数字藏品登记 ➡ 设置 PIN 码
    </div>
    <br />
    <br />
    <span style="color:#822E84">请输入设置您的 6 位 PIN 码：</span>
    <v-otp-input value="202255" length="6" type="number"></v-otp-input>
    <br />
    <v-img :src="NFT" class="animate__animated animate__bounceInDown"></v-img>
    <br />
    <br />
    <v-btn class="white--text" block color="#822E84" @click="nextStep">
      确认 PIN 码并开始卡片校验
    </v-btn>
    <v-img :src="NFC" height="70" width="70" color="pink" style="float:right;top:-55px;right:-10px;"></v-img>
  </div>
</template>

<script>
import NFT from "../assets/images/NFT-1.png";
import NFC from "../assets/images/NFC.gif";
export default {
  data: () => ({
    loading: false,
    NFT,
    NFC
  }),
  methods: {
    nextStep() {
      this.loading = true;
      // setTimeout(() => {
      //   this.$router.push("/bind/step-6");
      //   this.loading = false;
      // }, 2000);
      // const ndef = new NDEFReader();
      window.ndef = new NDEFReader();
      window.ndef.scan().then(() => {
        window.ndef.onreadingerror = (event) => {
          alert("【温馨提示】：NFC 读取错误！");
          this.loading = false;
        };
        window.ndef.onreading = (event) => {
          this.$router.push("/bind/step-6");
          this.loading = false;
        };
      }).catch(error => {
        alert("【温馨提示】：NFC 启动错误！");
        this.loading = false;
      });
    },
  },
};
</script>

<style lang="scss" scoped>
#step-4 {
  margin: auto 6vw;
}
</style>
